@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  font-family: "Montserrat",sans-serif!important;
}

:root {
  --cardbackground: #fff;
  --text-background: #000;
};

body[scheme="space_gray"] {
  --cardbackground: #5d5f61;
  --text-background: #fff;
}

.message_avatar, .message_avatar img {
  background: rgba(0, 29, 61, 0.08);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.08);
  width: 33px;
  height: 33px;
  border-radius: 50%;
}
.Preloader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--dynamic_gray);
}
.message_avatar a {
  cursor: pointer;
}

.message_wrapp, .message_comment_icon, .message_mark {
  background: #F4F4F4;
}

.message_wrapp {
  width: 70%;
  height: auto;
  padding: 20px 26px;
  margin-left: 18.5px;
  border-radius: 15px;
}

.message_title {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 21px;
}

.message_text {
  white-space: pre-wrap;
  font-size: 15px;
}

.message_time {
  margin-top: 11.5px;
  font-weight: 600;
  font-size: 14px;
  color: #CBCBCB;
}

.message_down_box {
  height: 40px;
  margin-top: 10px;
  margin-left: 50px;
  display: flex;
}

.message_comment_icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.message_comment_icon {
  display: flex;
  align-items: center;
}

.comment_icon {
  margin: auto;
  color: #4B4B4B;
}

.comment_icon:hover {
  cursor: pointer;
  color: #3F8AE0;
}

.message_mark {
  background: #F4F4F4;
  border-radius: 86px;
  padding: 12.5px 29px;
}

.wrapp_box_down {
  display: flex;
  position: absolute;
  right: 0;
}

.flex {
  display: flex;
}

.delete_mark {
  cursor: pointer;
  margin-right: 15px;
  color: #E64646;
}

.wrapper_send_money {
  display: flex;
}

.send_money_title {
  height: 233.76px;
  width: 50%;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  display: flex;
  align-items: center;
  letter-spacing: -0.333143px;
  color: #000;
  padding: 0 33px;
}

.send_money_inputs {
  width: 50%;
  margin: auto;
}

.ActivityPage,.TxView {
  padding: 16px;
  box-sizing: border-box;
}

.NumberView {
  border-radius: 14px;
  background: var(--background_suggestions);
  box-shadow: 0 0 8px 1px rgba(0,0,0,.04),0 8px 20px 0 rgba(0,0,0,.07);
  box-sizing: border-box;
  padding: 16px;
  margin-bottom: 12px;
}

.coin1 {
  position: absolute;
  top: 60%;
  left: 35%;
  width: 30vw;
  height: 30vw;
  border-radius: 100%;
  background: #d3b200;
  animation: spin 3s ease-in-out infinite;
}
.diceText {

        width: 100%;
        height: 100%;
        top: 30;
        left: 0;
        color: #fff;
        position: absolute;
        text-align: center;
        font-size: 100px;
        font-weight: 500;
        opacity: 0.8;
        animation: dice-show-result-text 1s linear both;
}
.diceImg {

        opacity: 0.8;
        animation: dice-show-result-text 1s linear both;
}
@keyframes dice-show-result-text {
  0% {
    opacity: 0;
    transform: scale(1.5, 1.5);
  }
  15% {
    opacity: 0;
    transform: scale(1.4, 1.4);
  }
  30% {
    opacity: 0;
    transform: scale(1.3, 1.3);
  }
  50% {
    opacity: 0;
    transform: scale(1.2, 1.2);
  }
  80% {
    opacity: 0;
    transform: scale(1.1, 1.1);
  }
  100% {
    opacity: 0.8;
    transform: scale(1, 1);
  }
}

