.coin1::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  content: '';
  border-radius: 100%;
  border: 1.25vw #ecc700 solid;
  transform: translate(-50%,-50%);
}

.coin1::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  content: '';
  border: 1vw #ffd700 solid;
  border-radius: 100%;
  transform: translate(-50%,-50%);
}

.coin1 span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: #ffd700;
  font-size: 5vw;
  font-family: impact;
}

@keyframes spin {
  0% {
    transform: scale(1,1)   rotateY(0deg)   translateY(0);
  }

  10% {
    transform: scale(1,1)   translateY(0);
  }

  30% {
    transform: scale(1,1)  rotateY(180deg) translateY(-210px);
  }

  50% {
    transform: scale(1,1) rotateY(360deg) translateY(0);
  }

  57% {
    transform: scale(1,1)     translateY(-7px);
  }

  64% {
    transform: scale(1,1)     translateY(0);
  }

  100% {
    transform: scale(1,1)   rotateY(360deg)   translateY(0);
  }
}

.NumberView__description {
  font-size: 14px;
  line-height: 14px;
  color: #909499;
  margin-bottom: 10px;
}

.NumberView__description,.NumberView__value {
  font-family: TT Commons,BlinkMacSystemFont,Roboto,Open Sans,Helvetica Neue,sans-serif;
  font-weight: 400;
}

.xui {
  text-decoration: none;
}

.NumberView__value {
  font-size: 32px;
  line-height: 32px;
  color: var(--text_primary);
  white-space: nowrap;
}

.NumberView__value:after {
  display: inline-block;
  width: 27px;
  height: 27px;
  content: "";
  background-image: url(https://vk.com);
  background-position: 50%;
  background-repeat: no-repeat;
  margin-left: 8px;
  vertical-align: -4px;
}

.NumberView__trend {
  padding-top: 5px;
  font-size: 15px;
  line-height: 16px;
  font-weight: 500;
  color: #909499;
}

.TabbarItem.games {
  color: rgba(255, 255, 255, 0.8);
  background: #4CAF50;
  background-image: linear-gradient(120deg, #ff0a6c 0%, #227ff 100%);
  box-shadow: 0 2px 15px 0 rgba(255, 193, 7, 0);
  border-radius: 12px;
  transition: all 120ms linear;
  position: relative;
  transform: translateY(0);
}

.TabbarItem--selected.games {
  color: rgba(255, 255, 255, 1);
  transform: translateY(-5px);
  box-shadow: 0 2px 15px 0 rgba(76, 175, 80, 0.4);
}

.send_money_inputs input {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 7.17055px;
  margin-bottom: 13px;
  padding: 13px;
  width: 80%;
  color: #000;
  border: 0;
}